:root {
  --colorBg: gray;
  --colorMb: dimgray;
}

.wrapper {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 20px;
}
.mainBlock {
  background: var(--colorMb);
  padding: 20px;
  border: seashell;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  border-radius: 10px;
}
.noteBlockWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.textArea {
  border: 3px solid #000;
  border-radius: 10px;
  outline: none;
  resize: none;
  height: 400px;
  width: 400px;
  padding: 5px;
  background: lightgray;
}

.notesNames {
  position: relative;
  margin-bottom: 10px;
  padding: 0 5px;
  width: 200px;
  
}

.buttonSave, .delBtn {
  margin-top: 5px;
  width: 100px;
  border-radius: 5px;
}

/* .titleWrapper {
  margin-top: 10px;
} */

.title {
  cursor: pointer;
  background:rgb(161, 125, 125) ;
  margin: 3px 20px 5px 0;
  padding: 0 5px;
  width: 180px;
}

.active {
  background: lightgrey;
}

.searchWrapper {
  margin-left: 20px;
}

.searchRes {
  cursor: pointer;
  color: white;
  background: #000;
}